import React from 'react';
import { Button } from 'reactstrap';

export default function RecordingItem({ meeting, handleClick }) {
    return (
        <tr>
            <td>{meeting.topic}</td>
            <td>{meeting.start_time}</td>
            <td>{meeting.duration}</td>
            <td>{meeting.timezone}</td>
            <td>
                <Button color="primary" size="sm" type="button" onClick={() => handleClick(meeting)}>
                    Upload to S3
                </Button>
            </td>
        </tr>
    )
}
