import React from "react";
import {
  Card,
  CardHeader,
  CardBody,
  Form,
  Container,
  Row,
  Col
} from "reactstrap";
import { useDispatch, useSelector } from 'react-redux';

function Profile(props) {
  const user = useSelector(state => state.users.user);
  const zoomUser = useSelector(state => state.zoomData.zoomUser);

  return (
    <>
      <Container fluid>
        <Row>
          <Col>
            <Card>
              <CardHeader>
                <Row className="align-items-center">
                  <Col xs="8">
                    <h3 className="mb-0">My Profile</h3>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                <Form>
                  <h6 className="heading-small text-muted mb-4">
                    Account information
                  </h6>
                  <div className="pl-lg-4">
                    <Row>
                      <Col md="6">
                        Username: {user.username}
                      </Col>
                      <Col md="6">
                        Email: {user.email}
                      </Col>
                    </Row>
                  </div>
                  <hr className="my-4" />

                  <h6 className="heading-small text-muted mb-4">
                    Zoom Account information
                  </h6>
                  {
                    zoomUser &&
                    <div className="pl-lg-4">
                      <Row>
                        <Col md="6" className="py-3">
                          First Name: {zoomUser.first_name}
                        </Col>
                        <Col md="6" className="py-3">
                          Last Name: {zoomUser.last_name}
                        </Col>
                        <Col md="6" className="py-3">
                          Email: {zoomUser.email}
                        </Col>
                        <Col md="6" className="py-3">
                          Phone: {zoomUser.phone}
                        </Col>
                        <Col md="6" className="py-3">
                          Location: {zoomUser.location}
                        </Col>
                        <Col md="6" className="py-3">
                          Timezone: {zoomUser.timezone}
                        </Col>
                      </Row>
                    </div>
                  }
                  <hr className="my-4" />
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Profile;
